import { OperationState } from './state'
import { getStoreAccessors } from 'typesafe-vuex'
import { State } from '../state'

export const getters = {
  operationsActivities: (state: OperationState) => state.activities,
  operationsOneActivity: (state: OperationState) => (activityId: number) => {
    const filteredActivities = state.activities.filter((activity) => activity._id === activityId)
    if (filteredActivities.length > 0) {
      return { ...filteredActivities[0] }
    }
  },
  operationsCustomers: (state: OperationState) => state.customers,
  operationsOneCustomer: (state: OperationState) => {
    return state.customer
  },
  operationsSessions: (state: OperationState) => state.sessions,
  operationsOneSession: (state: OperationState) => {
    return state.sessions
  },
}

const { read } = getStoreAccessors<OperationState, State>('')

export const readOperationsOneActivity = read(getters.operationsOneActivity)
export const readOperationsActivities = read(getters.operationsActivities)

export const readOperationsOneCustomer = read(getters.operationsOneCustomer)
export const readOperationsCustomers = read(getters.operationsCustomers)

export const readOperationsOneSession = read(getters.operationsOneSession)
export const readOperationsSessions = read(getters.operationsSessions)
