import { IActivityProfile, ICustomerProfile, ISessionProfile } from '@/interfaces'
import { OperationState } from './state'
import { getStoreAccessors } from 'typesafe-vuex'
import { State } from '../state'

export const mutations = {
  setActivities(state: OperationState, payload: IActivityProfile[]) {
    state.activities = payload
  },
  setActivity(state: OperationState, payload: IActivityProfile) {
    const Activities = state.activities.filter((user: IActivityProfile) => Activities.id !== payload._id)
    Activities.push(payload)
    state.activities = Activities
  },
  setCustomers(state: OperationState, payload: ICustomerProfile[]) {
    state.customers = payload
  },
  setCustomer(state: OperationState, payload: ICustomerProfile | null) {
    state.customer = payload
  },
  setSessions(state: OperationState, payload: ISessionProfile[]) {
    state.sessions = payload
  },
  setSessionById(state: OperationState, payload: ISessionProfile[]) {
    console.log('sessionById payload: ', payload)
    state.sessions = payload
  },
}

const { commit } = getStoreAccessors<OperationState | any, State>('')

export const commitSetActivities = commit(mutations.setActivities)
export const commitSetActivity = commit(mutations.setActivity)

export const commitSetCustomers = commit(mutations.setCustomers)
export const commitSetCustomer = commit(mutations.setCustomer)

export const commitSetSessions = commit(mutations.setSessions)
export const commitSetSessionById = commit(mutations.setSessionById)
