import { api } from '@/api'
import { getStoreAccessors } from 'typesafe-vuex'
import { ActionContext } from 'vuex'
import { OperationState } from './state'
import {
  commitSetActivities,
  commitSetCustomers,
  commitSetCustomer,
  commitSetSessions,
  commitSetSessionById,
} from './mutations'
import { dispatchCheckApiError } from '../main/actions'
import { State } from '../state'
import { ICustomerProfile, ICustomerSearch } from '@/interfaces'
import { AxiosError } from 'axios'

type MainContext = ActionContext<OperationState, State>

export const actions = {
  async actionGetActivities(context: MainContext) {
    try {
      const response = await api.getActivities()
      if (response) {
        commitSetActivities(context, response.data)
      }
    } catch (error: AxiosError | any) {
      await dispatchCheckApiError(context, error)
    }
  },
  async actionGetCustomerActivities(context: MainContext, customerId: number) {
    try {
      const response = await api.getCustomerActivities(customerId)
      if (response) {
        commitSetActivities(context, response.data)
      }
    } catch (error: AxiosError | any) {
      await dispatchCheckApiError(context, error)
    }
  },
  async actionGetCustomers(context: MainContext, params: ICustomerSearch) {
    try {
      const response = await api.getCustomers(params)
      if (response) {
        commitSetCustomers(context, response.data)

        commitSetCustomer(context, null)
      }
    } catch (error: AxiosError | any) {
      await dispatchCheckApiError(context, error)
    }
  },
  async actionSetCustomer(context: MainContext, customer: ICustomerProfile) {
    commitSetCustomer(context, customer)
  },
  async actionGetCustomerByEmail(context: MainContext, email: string) {
    try {
      const response = await api.getCustomerByEmail(email)
      if (response) {
        if (response.data) {
          commitSetCustomer(context, response.data)
        }
      }
    } catch (error: AxiosError | any) {
      await dispatchCheckApiError(context, error)
    }
  },
  async actionGetSessions(context: MainContext) {
    try {
      const response = await api.getSessions()
      if (response) {
        commitSetSessions(context, response.data)
      }
    } catch (error: AxiosError | any) {
      await dispatchCheckApiError(context, error)
    }
  },
  async actionGetSessionById(context: MainContext, session_id: string) {
    try {
      const response = await api.getSessionById(session_id)
      if (response) {
        commitSetSessionById(context, response.data)
      }
    } catch (error: AxiosError | any) {
      await dispatchCheckApiError(context, error)
    }
  },
}

const { dispatch } = getStoreAccessors<OperationState | any, State>('')

export const dispatchGetCustomerActivities = dispatch(actions.actionGetCustomerActivities)

export const dispatchGetCustomers = dispatch(actions.actionGetCustomers)

export const dispatchSetCustomer = dispatch(actions.actionSetCustomer)

export const dispatchGetCustomerByEmail = dispatch(actions.actionGetCustomerByEmail)

export const dispatchGetSessions = dispatch(actions.actionGetSessions)

export const dispatchGetSessionById = dispatch(actions.actionGetSessionById)
