import axios from 'axios'
import { apiUrl } from '@/env'
import { IActivityProfile, ICustomerProfile, ISessionProfile, ICustomerSearch, IAuth0User, IUserCreate } from './interfaces'

const getHeaders = () => {
  const accessToken = localStorage.getItem('access_token');

  return {
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${accessToken}`,
    },
  }
}

export const api = {
  async getActivities() {
    const headers = await getHeaders()

    return axios.get<IActivityProfile[]>(`${apiUrl}/api/v1/customer/`, headers)
  },

  async getCustomerActivities(customerId) {
    const headers = await getHeaders()

    return axios.get<IActivityProfile[]>(`${apiUrl}/activity/customer/${customerId}`, headers) // edge read-replica
  },

  async getCustomers({ firstName, lastName, ssnLast4, dob }: ICustomerSearch) {
    const url = new URL(`${apiUrl}/customer/`)

    if (firstName) {
      url.searchParams.append('firstName', firstName)
    }
    if (lastName) {
      url.searchParams.append('lastName', lastName)
    }
    if (ssnLast4) {
      url.searchParams.append('ssnLast4', ssnLast4.toString())
    }
    if (dob) {
      url.searchParams.append('dob', dob)
    }
    const headers = await getHeaders()

    const results = await axios.get<ICustomerProfile[]>(url.href, headers)
    return results
  },

  async getCustomerByEmail(email: string) {
    const headers = await getHeaders()

    return await axios.get<ICustomerProfile>(`${apiUrl}/customer/email/${email}`, headers)
  },

  async getSessions() {
    const headers = await getHeaders()

    return await axios.get<ISessionProfile[]>(`${apiUrl}/api/v1/session/`, headers)
  },

  async getSessionById(sessionId) {
    const headers = await getHeaders()

    return await axios.get(`${apiUrl}/api/v1/session/${sessionId}`, headers)
  },

  async postLoginSuccess(user: IAuth0User) {
    const headers = await getHeaders()
    const { email, name, sub } = user
    const firstName = name.split(' ')[0]
    const lastName = name.split(' ')[1]
    const userBody: IUserCreate = {
      externalId: sub,
      firstName,
      lastName,
      email,
    }
    return await axios.post(`${apiUrl}/user/`, userBody, headers)
  }
}
