
























import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import { api } from '../api'
import { dispatchSetUser } from '../store/main/actions'

@Component
export default class Home extends Vue {
  @Prop() private msg!: string

  public userEmail = this.user
  $auth: any

  @Watch('user')
  public loginSuccess() {
    api.postLoginSuccess(this.$auth.user)
    dispatchSetUser(this.$store, this.$auth.user)
  }

  get user() {
    return this.$auth.user.email
  }

  public login() {
    this.$auth.loginWithRedirect()
  }

  public logout() {
    this.$auth.logout({
      returnTo: window.location.origin,
    })
  }

  public consolelog() {
    console.log('this.$store: ', this)
    console.log('this.$auth: ', this.$auth)
  }
}
