import Vue from 'vue'
import Router from 'vue-router'

import { authGuard } from './auth'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      props: {
        msg: 'Welcome to Stargate',
      },
      component: () =>
        import(
          /* webpackChunkName: "main-operations-homepage" */
          './components/Home.vue'
        ),
    },
    {
      path: '/activity/activities/all',
      name: 'activities',
      props: true,
      component: () =>
        import(
          /* webpackChunkName: "main-operations-activity-activities" */
          './views/operations/activity/Activity.vue'
        ),
    },
    {
      path: '/customer/customers/all',
      // path: '/customer',
      component: () =>
        import(
          /* webpackChunkName: "main-operations-customer-customers" */
          './views/operations/customer/Customers.vue'
        ),
      beforeEnter: authGuard,
    },
    {
      path: '/session/sessions/all',
      name: 'session',
      props: true,
      component: () =>
        import(
          /* webpackChunkName: "main-operations-session-sessions" */
          './views/operations/sessions/Sessions.vue'
        ),
    },
    {
      path: '/user/users/all',
      component: () =>
        import(
          /* webpackChunkName: "main-operations-user-users" */
          './views/operations/user/Users.vue'
        ),
    },
    {
      path: '/*',
      redirect: '/',
    },
  ],
})
